<template>
  <div>
    <div class="p3">
      <a class="tertiary font-title" @click="signOut"><i class="fas fa-sign-out-alt fa-rotate-180 m1-right"></i>EXIT</a>
    </div>

    <div class="p3 p1-top">
      <div v-if="property" class="m4-bottom">
        <p class="font-title light font3">{{property.description}}</p>
        <p class="tertiary line180">{{property.city}}, {{property.state}} ({{property.county}})</p>
      </div>

      <title-label title="forms" class="m4-top"></title-label>
      <div class="m4-bottom flex-row">
        <button v-if="!tds" class="flex-one button alt m2-right" @click="$router.push('/disclosures?form=tds')">
          <i class="fas fa-poll-h m1-right"></i>FILL TDS
        </button>
        <button v-if="tds" class="flex-one button alt m2-right" @click="showPdf(tds.Key)">
          <i class="fas fa-check-circle fa-inverse m1-right"></i>VIEW TDS
        </button>

        <button v-if="!ssc" class="flex-one button alt" @click="$router.push('/disclosures?form=ssc')">
          <i class="fas fa-poll-h m1-right"></i>FILL SSC
        </button>
        <button v-if="ssc" class="flex-one button alt" @click="showPdf(ssc.Key)">
          <i class="fas fa-check-circle m1-right"></i>VIEW SSC
        </button>
      </div>

      <div class="appointments">
        <title-label title="Appointments"></title-label>
        <div v-for="(appointment, i) in appointments" :key="i" class="appointment-wrapper" :class="[appointment.status]">
          <h3 class="uppercase tertiary2 m4-top m1-bottom font-title"><span>{{appointment.status}}</span></h3>
          <appointment :appointment="appointment" class="m2-bottom" @change="apptChanged"></appointment>
        </div>
        <p v-if="appointments.length === 0" class="tertiary">No appointments</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import TitleLabel from '@/components/TitleLabel'
import Appointment from '@/views/Appointment'

export default {
  name: 'Client',
  components: { TitleLabel, Appointment },
  data () {
    return {
      appointments: [],
      property: undefined,
      files: []
    }
  },
  props: ['clientId'],
  inject: ['userClient', 'apptHelper'],
  computed: {
    ...mapGetters(['session']),
    tds () {
      return this.files.find(f => f.Key.includes('tds.pdf'))
    },
    ssc () {
      return this.files.find(f => f.Key.includes('ssc.pdf'))
    }
  },
  methods: {
    ...mapActions(['setSession', 'setPdf']),
    signOut () {
      this.setSession(undefined)
      this.$router.push('/')
    },
    showPdf (key) {
      this.setPdf(this.userClient.getFileUrl(this.clientId, key))
      this.$router.push(`/pdf`)
    },
    fetchAppointments () {
      const self = this
      this.userClient.getAppointments(this.clientId)
        .then(appointments => {
          this.appointments = appointments
          this.apptHelper.sortAppointments(this.appointments)
        })
      this.userClient.getProperty(this.clientId)
        .then(property => {
          self.property = property
        })
    },
    fetchFiles () {
      this.userClient.getFiles(this.clientId)
        .then(files => {
          this.files = files
        })
    },
    apptChanged (updatedAppointment) {
      const index = this.appointments.findIndex(a => a.id === updatedAppointment.id)
      this.appointments[index] = updatedAppointment
      this.apptHelper.sortAppointments(this.appointments)
    }
  },
  mounted () {
    this.fetchAppointments()
    this.fetchFiles()
  }
}
</script>

<style lang="scss" scoped>
.appointments {
  .pending h3 span { border-bottom: solid 1px #24c1ff; }
  .scheduled h3 span { border-bottom: solid 1px #0461c2; }
  .completed h3 span { border-bottom: solid 1px #767569; }
  .canceled h3 span { border-bottom: solid 1px #fc0000; }
  .declined h3 span { border-bottom: solid 1px #ff9d10; }
  .pending ~ .pending, .scheduled ~ .scheduled, .declined ~ .declined, .completed ~ .completed, .canceled ~ .canceled {
    h3 {
      display: none;
    }
  }
}
</style>
